<template>
<div class="" style="width: 100%;">
  <mu-paper :z-depth="1" class="home-list relative">
    <mu-appbar color="grey900" style="text-align: center">
      <!--左边按钮-->
      <mu-button icon slot="left" @click="logOut">
        <mu-icon value="注销"></mu-icon>
      </mu-button>
      超零工作室
    </mu-appbar>
    <div class="" style="overflow-y: auto" :style="{'height':windowHeight+ 'px'}">
      <mu-list style="padding-bottom: 80px">
        <mu-sub-header>
        <div class="width100 h-60px-center text-center" style="margin-bottom: -30px;height: 80px;">
          <span class="before-after-line  font-bold font16 ">活动列表</span>
        </div>
        </mu-sub-header>
        <div>
          <mu-list-item class="border-bottom-gray" :ripple="false" v-for="(item,index) in goodsList" :key="index">
            <div class="SZ-flex padding-tb16 width100">
              <div class="img-box"  style="width: 120px;margin-right: 8px">
                <img :src="item.productUrl" width="100%">
              </div>
              <div class="width100">
                <div class="font-bold fontXL">{{item.activeName}}</div>
                <div class="">
                  <div class="fontM">
                    {{item.activeStartTime}}-{{item.activeEndTime}} <div class="">{{item.address}}</div>
                  </div>
                  <div class="" style="padding-top: 8px">
                    <span class="fontS bg-red400 tag" style="padding: 2px 4px;color: #ffffff" v-for="(item2,idx) in item.tags" :key="idx">{{item2}}</span>
                  </div>
                </div>
                <div class="flex-wrap-between width100" style="padding-top: 10px">
                  <div class="">
                    <div class=""><span class="padding-r16 fontXL font-bold red400">￥{{item.salePrice}}</span><span class="fontS">{{item.signed}}/{{item.limit}}人开团</span></div>
                    <div class="grey600 fontS ">"{{item.desc}}"</div>
                  </div>
                  <div class="tags red400 fontS border-r400" style="padding: 4px 12px;" v-if="new Date()<new Date(item.activeStartTime)">可预约</div>
                  <div class="tags red400 fontS border-r400" style="padding: 4px 12px;" v-else-if="new Date()>=new Date(item.activeStartTime) && new Date()<=new Date(item.activeEndTime)">进行中</div>
                  <div class="tags red400 fontS border-r400" style="padding: 4px 12px;" v-else-if="new Date()>new Date(item.activeEndTime)">已结束</div>
                </div>
                <div class="text-right">
                  <van-button type="danger" @click="delActive(item)">删除</van-button>
                  <van-button type="warning" @click="$router.push({path: '/change',query:{id:item._id}})">编辑</van-button>
                </div>

              </div>
            </div>
          </mu-list-item>
      </div>
    </mu-list>
    </div>
    <div class="btn-botton fontXL text-center flex" >
      <div class="f-item bg-red400 color-fff" @click="$router.push({path: '/change'})">新增</div>
      <div class="f-item bg-blue500 color-fff" @click="$router.push({path: '/sign'})">用户报名</div>
    </div>
  </mu-paper>
</div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "home",
    components: {
    },
    data() {
      return {
        windowHeight:window.screen.height,
        swiperOption:{
          direction: "horizontal",
          loop: true,
          slidesPerView: "auto",
          autoplay:true,//等同于以下设置
          loopedSlides: 3,
          centeredSlides: true,
          centeredSlidesBounds: true,
          observer: true,
          observeParents: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        },
        goodsList:[],
        page:1,
        pageSize:10,
    }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      // this.getTotalPage()
      this.getGoodsList()

    },
    computed: {},
    methods: {
      logOut(){
        axios.post("/users/logout").then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$store.commit("updateUserInfo",'');
            this.$router.push({path: '/'})
          }
        })
      },
      goDetail(){
        console.log(1)
        this.$router.push({
          path: '/detail',
          params:'id'
        })
      },
      getGoodsList(flag){
        var param = {
          page:this.page,
          pageSize:this.pageSize,
          sort:this.sortFlag?1:-1,
          priceLevel:''
        };
        // this.loading = true;
        this.$axios.get("/active/list",{
          params:param
        }).then((response)=>{
          var res = response.data;
          // this.loading = false;
          if(res.status=="0"){
            if(flag){
              this.goodsList = this.goodsList.concat(res.result.list);

              if(res.result.count==0){
                this.busy = true;
              }else{
                this.busy = false;
              }
            }else{
              this.goodsList = res.result.list;
              this.busy = false;
            }
          }else{
            this.goodsList = [];
          }
        });
      },
      delActive(item){
        axios.get("/active/deleteActive",{params:{id:item._id}}).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$toast.success(res.msg);
            // setTimeout(()=>{this.$router.push('/list')},1000)
            this.getGoodsList()
          }
        })
      }
    }
  }
</script>

<style scoped>
  .btn-botton{
    position: absolute;
    bottom: 50px;
    width: 100%;
    height: 50px;
    line-height: 50px;
  }
  .home-list {
    width: 100%;
    height: 100%;
    overflow-y: auto;
  }
  /deep/ .mu-icon-button{
    font-size: 14px!important;
    width: 52px;
    border-radius:5px;
    padding: 10px 6px!important;
    border: 1px #ffffff solid;
    height: 30px;
  }
  /deep/ .mu-item{
    padding: 16px;
    height: auto!important;
  }
  .swiper-slide {
    /*float: left;*/
      transition: 300ms;
    opacity: .5;
  }
  .swiper-item{
    position: absolute;
    width: 200px;
    height: 112.5px;
    top: 50%;
    left: 50% ;
    transform: translate(-50%,-50%);
    border-radius: 5px;
  }

  .swiper-slide-active {
    z-index: 9;
    transform: scale(1.5);
    opacity: 1;
  }

</style>



