<template>
  <div id="app" class="fillcontain" style="height: 100%">
    <div class="" style="height: 100%">
<!--      <keep-alive>-->
<!--        <router-view></router-view>-->
<!--      </keep-alive>-->
      <router-view></router-view>
    </div>
    <div class="g-footer-container" v-if="$route.name !== 'login' && $route.name !== 'register'&& $route.name !== 'GoodsDetail'&& $route.name !== 'list'&& $route.name !== 'change'&& $route.name !== 'sign'">
      <c-tabbar/>
    </div>
  </div>
</template>

<script>
  import CTabbar from './components/tabbar';
  export default {
    name: "app",
    data() {
      return {
        isRouterAlive: true
      };
    },
    components: {
      CTabbar
    },
    methods: {
    }
  };
</script>
<style>

  .left{
    width:20%;
    border: 1px solid gray;
    float: left;
  }
  #app {
    font-family: "Source Han Sans CN";
    height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    background-color: #eaeaea;
  }
</style>



