<script>
  export default {
    name: "home",
    components: {
    },
    data() {
      return {
        windowHeight:window.screen.height,
        swiperOption:{
          direction: "horizontal",
          loop: true,
          slidesPerView: "auto",
          autoplay:true,//等同于以下设置
          loopedSlides: 3,
          centeredSlides: true,
          centeredSlidesBounds: true,
          observer: true,
          observeParents: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        },
        priceChecked:'all',
        goodsList:[]
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      // this.getTotalPage()
      this.getGoodsList()
    },
    computed: {},
    methods: {
      getGoodsList(flag){
        var param = {
          page:this.page,
          pageSize:this.pageSize,
          sort:this.sortFlag?1:-1,
          priceLevel:this.priceChecked
        };
        // this.loading = true;
        this.$axios.get("/active/list",{
          params:param
        }).then((response)=>{
          var res = response.data;
          if(res.status=="0"){
            if(flag){
              this.goodsList = this.goodsList.concat(res.result.list);
              if(res.result.count==0){
                this.busy = true;
              }else{
                this.busy = false;
              }
            }else{
              this.goodsList = res.result.list;
              this.busy = false;
            }
          }else{
            this.goodsList = [];
          }
        });
      }
    },
  }
</script>

