<template>
  <div class="middleCenter middleCenter8" style="background: rgb(255, 255, 255);height: 100%;">
    <div class="formMiddleContent formMiddleContent8 moduleContent" _innertype="0">
      <div id="mLogin" style="position: relative">
        <div class="text-center padding-tb16 border-bottom-gray relative">
          <div class="" style="position: absolute;top: 0;padding: 18px 15px;background:#999999;opacity: .8" @click="$router.push('/login')">返回</div>
          <span
          class="fontL">注册</span></div>
        <div class="loginContent mloginContent flex">
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex input-must" style="line-height: 50px;height: 50px;">
              cn：
              <input type="text" maxlength="10"
                     class="admin-text "
                     placeholder="请输入圈名"
                     v-model="form.cosName"
              >
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex input-must" style="line-height: 50px;height: 50px;">
              手机号：
              <input type="text" maxlength="11"
                     class="admin-text "
                     placeholder="手机号作为登录帐号"
                     v-model="form.userName"
              >
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex input-must" style="line-height: 50px;height: 50px;">
              密码：
              <input type="password" maxlength="20"
                     class="password-text "
                     placeholder="请输入密码"
                     v-model="form.userPwd"
              >
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex input-must" style="line-height: 50px;height: 50px;">
              确认密码：
              <input type="password" maxlength="20"
                     class="password-text "
                     placeholder="请再次输入密码"
                     v-model="confirmPSW"
              >
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex input-must" style="line-height: 50px;height: 50px;">
              <div style="white-space: nowrap">QQ或微信：</div>
              <input type="text" maxlength="50"
                     class="admin-text "
                     placeholder="请输入QQ或微信"
                     v-model="form.QQorWeChat"
              >
            </div>
          </div>
          <div class="g_globalLine forAcctLogin input-item text-center login-item" @click="register">
            注册
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "register",
    data() {
      return {
        form: {
          "userName": "",
          "userPwd": "",
          "cosName": "",
          "QQorWeChat": ""
        },
        confirmPSW:''
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
    },
    computed: {},
    methods: {
      register(values) {
        for (let idx in this.form){
          if (this.form[idx] === '' || this.form[idx] === null){
            this.$toast('请输入完整再注册')
            return;
          }
        }
        if (this.form.userPwd.length<8) {
          this.$toast('请输入8位以上的密码')
          return;
        }
        if (this.form.userPwd !== this.confirmPSW) {
          this.$toast('两次输入密码不一致')
          return;
        }
        axios.post("/users/register",this.form).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$toast.success(res.msg);
            this.$router.push("/login")
            return;
          }
          this.$toast.fail(res.msg);
        });
      }
    }
  }
</script>

<style scoped>
  .loginContent{
    width: 100%;
    padding-top: 100px;
    align-items: center;
    flex-direction:column;
  }
  .login-item{
    margin: 30px 0px 17px 0;
    height: 50px;
    line-height: 50px;
    background: #292929;
    color: #eeeeee;
  }
  .input-must:before{
    content: '*';
    color: #de181b;
  }
  .input-item{
    position: relative;
    width: calc(100% - 60px);
    white-space: nowrap;
  }
</style>



