</template>

<script>
  import axios from "axios";

  export default {
    name: "myCenter",
    data() {
      return {
        active2: 0,
        params:{},
        widowsHeight:window.screen.height - (48 +55 +85 +106)
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      if (this.$store.getters.getUserInfo){
        this.getUserInfo(this.$store.getters.getUserInfo.userName);
      }else {
        this.checkLogin(()=>{
          this.getUserInfo(this.$store.getters.getUserInfo.userName);
        });
      }
    },
    computed: {},
    methods: {
      checkLogin(cb){
        axios.get("/users/checkLogin").then((response)=>{
          var res = response.data;
          var path = this.$route.pathname;
          if(Number(res.status)===0){
            this.$store.commit("updateUserInfo",res.result);
            cb()
          }else{
          }
        });
      },
      getUserInfo(userName){
        axios.post("/users/getUserInfo",{userName:userName}).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.params = res.result;
          }
        })
      },
      logOut(){
        axios.post("/users/logout").then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.$store.commit("updateUserInfo",'');
            this.$router.push({path: '/'})
          }
        })
      },
      goDetail(){
        console.log(1)
        this.$router.push({path: '/detail'})
      },
      goChangePSW(){
        this.$router.push('/changePSW')
      }
    }
  }
</script>

<style scoped>
  >>>.mu-item{
