<template>
  <div class="h-100" style="overflow-y: auto;background:#ffffff;">
    <van-cell-group>
      <van-cell title="上传海报" required/>
      <div class="padding-10-16">
        <van-uploader v-model="fileList" :after-read="afterRead" :before-read="beforeRead" :max-count="1"/>
      </div>
    </van-cell-group>
      <div class="border-bottom-gray">
        <van-field required v-model="qData.activeName" placeholder="请输入活动名称" label="活动名称"/>
      </div>
    <div class="border-bottom-gray">
      <van-field required v-model="date" @click="show =true" readonly placeholder="选择活动时间" label="活动时间"/>
      <van-calendar v-model="show"  type="range" @confirm="onConfirm" />
    </div>
    <div class="border-bottom-gray">
        <van-field required v-model="qData.address" placeholder="请输入活动地点" label="活动地点"/>
    </div>
    <div class="border-bottom-gray">
      <div class="flex-center">
        <van-field required class="f-item" v-model="tagName" placeholder="请输入标签名" label="活动标签"/>
        <div class="fontL bg-red400" style="padding: 10px 12px;color: white" @click="addTags(tagName)">添加</div>
      </div>
      <van-cell-group>
        <div class="padding-10-16">
          <van-tag
            v-for="(item,idx) in this.qData.tags"
            :key="idx"
            closeable
            size="medium"
            type="danger"
            @close="close(item)"
            class="tag-item"
          >
            {{item}}
          </van-tag>
        </div>
      </van-cell-group>
    </div>
    <div class="border-bottom-gray">
      <van-field required v-model="qData.salePrice" placeholder="请输入价格" label="价格"/>
    </div>
    <div class="border-bottom-gray">
      <van-field required v-model="qData.limit" placeholder="最大报名人数" label="最大报名人数"/>
    </div>
    <div class="border-bottom-gray">
      <van-field
              required
        v-model="qData.desc"
        rows="1"
        autosize
        label="简介"
        type="textarea"
        placeholder="请输入简介"
        maxlength="10"
      />
    </div>
    <div class="border-bottom-gray">
      <van-field
        v-model="qData.text"
        rows="1"
        autosize
        label="详细介绍"
        type="textarea"
        placeholder="详细介绍"
      />
    </div>
    <van-cell-group>
      <van-cell title="图文详情" required/>
      <div class="padding-10-16">
        <van-uploader v-model="qData.imgList" :after-read="afterRead" :before-read="beforeRead2" />
      </div>
    </van-cell-group>
    <div class="flex">
      <van-button type="primary" class="f-item" @click="addActive">提交</van-button>
      <van-button type="warning" class="f-item" @click="$router.push('/list')">取消</van-button>
    </div>
    <!--<div class="rich-text">
    <editor :catchData="catchData"></editor>
    </div>-->
  </div>
</template>

<script>
  // import editor from "../components/wangEnduit/editor";
  import Toast from "vant/lib/toast";
  import axios from "axios";
  export default {
    name: "addOrEdit",
    data() {
      return {
        tagName:'',
        message:'',
        show2: {
          primary: true,
          success: true,
        },
        // fileList: [
        //   {
        //     url: 'https://img.yzcdn.cn/vant/leaf.jpg',
        //     status: 'uploading',
        //     message: '上传中...',
        //   },
        //   {
        //     url: 'https://img.yzcdn.cn/vant/tree.jpg',
        //     status: 'failed',
        //     message: '上传失败',
        //   },
        // ],
        value:'',
        show: false,
        date:'',
        inputContent: '',
        content: '',
        // 七牛
        qiNiu: {
          AccessKey: 'wbe4CXzOOWmWpTWbTlzX5_WkYmi4jRoy6i78ykTI',
          SecretKey: 'qL3OuZQnBkvPklAOA48H21W6WpvB3DhwpVFZ9h5Q'
        },
        QiniuData: {
          name: '',
          key: '', // 图片名字处理
          token: '' // 七牛云token
        },
        domain: 'https://upload-z2.qiniup.com', // 七牛云的上传地址（华南区）
        qiniuaddr: 'http://cdn.superzerohome.fun', // 七牛云的图片外链地址
        uploadPicUrl: '', // 提交到后台图片地址
        fileList: [],
        fileList2:[],
        loading: false,
        qData:{
          "activeName":'',
          "salePrice":'',
          "productUrl": '',
          "address": '',
          "activeStartTime": '',
          "activeEndTime": '',
          "tags": [],
          "limit": '',
          "signed": 0,
          "desc": '',
          "text": '',
          "imgList" :[]
        }
      }
    },
    components:{
      // editor
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      if (this.$route.query.id){
        this.getActiveDetail(this.$route.query.id)
      }
    },
    methods: {
      getActiveDetail(id){
        let that =this;
        axios.get("/active/getActiveDetail",{params:{id:id}}).then((response)=>{
          let res = response.data;
          if(res.status=="0"){
            that.qData = res.result;
            that.fileList = [{url:res.result.productUrl}]
            that.date = `${res.result.activeStartTime} - ${res.result.activeEndTime}`
          }else {
            this.$toast(res.msg)
          }
        });
      },
      addActive(){
        for (let idx in this.qData){
          if ((this.qData[idx] === '' ||this.qData[idx] === null)&&idx!=='text'){
            this.$toast('请填写完整后再提交');
            return;
          }
        }
        if (this.$route.query.id){
          // 更新
          axios.post("/active/updateActive",this.qData).then((response)=>{
            let res = response.data;
            if(res.status=="0"){
              this.$toast(res.msg)
              this.$router.push('/list')
            }else {
              this.$toast(res.msg)
            }
          });
        }else {
          // 新增
          axios.post("/active/addActive",this.qData).then((response)=>{
            let res = response.data;
            if(res.status=="0"){
              this.$toast(res.msg)
              this.$router.push('/list')
            }else {
              this.$toast(res.msg)
            }
          });
        }
      },
      addTags(item){
        if (item){
          this.qData.tags.push(item);
          this.tagName = '';
        }
      },
      getToken(cb){
        /*const CryptoJS = require('crypto-js')
        let timestamp; // 当前的时间戳
        timestamp = Number(Math.round(new Date().getTime() / 1000)) + 36000
        let putPolicy = {'scope':`szh666666:${timestamp}.${file.type.split('/')[1]}`, 'deadline': timestamp}
        let encodedPutPolicy = CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(JSON.stringify(putPolicy)))
        let encodedSign = CryptoJS.enc.Base64.stringify(CryptoJS.HmacSHA1(encodedPutPolicy, this.qiNiu.SecretKey)) // 第一个参数为加密字符串，第二个参数为公共秘钥
        let token = this.qiNiu.AccessKey + ':' + encodedSign + ':' + encodedPutPolicy*/
        axios.get("/active/token").then((response)=>{
          var res = response.data;
          this.QiniuData.token = res.upToken
          cb()
        });
      },
      beforeRead (file) {
        let that = this;
        var qiniu = require('qiniu-js')
        this.getToken(()=>{
          return new Promise((resolve, reject) => {
            if (file.type !== 'image/jpeg'&&file.type !== 'image/png') {
              let msg = '请上传 jpg 格式图片'
              Toast(msg)
              reject(msg)
            } else {
              var config = {
                useCdnDomain: true,
                region: null
              };
              var putExtra = {
                fname: file.name,
                params: {},
                mimeType: null
              };
              var observable = qiniu.upload(file, `${Number(Math.round(new Date().getTime() / 1000)) + 36000}.${file.type.split('/')[1]}`, that.QiniuData.token, putExtra, config)
              var subscription = observable.subscribe({
                next(res){
                  // ...
                },
                error(err){
                  // ...
                  Toast('上传失败')
                },
                complete(res){
                  console.log(res,'res')
                  Toast('上传成功')
                  let item = {
                    url:that.qiniuaddr + '/' +res.key
                  }
                  that.qData.productUrl=item.url
                  that.fileList.push(item)
                }
              }) // 上传开始
              // request({
              //   url: 'http://upload-z2.qiniup.com',
              //   method: 'POST',
              //   data: data,
              //   headers: {
              //     'Content-Type': 'multipart/form-data'
              //   }
              // }).then(response => {
              //   that.fileList = [{
              //     url: that.qiniuaddr + '/' + response.key,
              //     isImage: true
              //   }]
              //   resolve(response)
              // })
            }
          })
        });
      },
      beforeRead2 (file) {
        this.getToken(file);
        let that = this;
        var qiniu = require('qiniu-js')
        return new Promise((resolve, reject) => {
          if (file.type !== 'image/jpeg'&&file.type !== 'image/png') {
            let msg = '请上传 jpg 格式图片'
            Toast(msg)
            reject(msg)
          } else {
            var config = {
              useCdnDomain: true,
              region: null
            };
            var putExtra = {
              fname: file.name,
              params: {},
              mimeType: null
            };
            // debugger
            var observable = qiniu.upload(file, `${Number(Math.round(new Date().getTime() / 1000)) + 36000}.${file.type.split('/')[1]}`, that.QiniuData.token, putExtra, config)
            var subscription = observable.subscribe({
              next(res){
                // ...
              },
              error(err){
                // ...
                Toast('上传失败')
              },
              complete(res){
                console.log(res,'res')
                Toast('上传成功')
                let item = {
                  url:that.qiniuaddr + '/' +res.key
                }
                that.qData.imgList.push(item)
              }
            }) // 上传开始
              // request({
              //   url: 'http://upload-z2.qiniup.com',
              //   method: 'POST',
              //   data: data,
              //   headers: {
              //     'Content-Type': 'multipart/form-data'
              //   }
              // }).then(response => {
              //   that.fileList = [{
              //     url: that.qiniuaddr + '/' + response.key,
              //     isImage: true
              //   }]
              //   resolve(response)
              // })
          }
        })
      },
      catchData(value) {
        this.$axios.post('')
        this.content = value //在这里接受子组件传过来的参数，赋值给data里的参数
      },
      close(item) {
        let index = this.qData.tags.indexOf(item);
        if (index > -1) {
          this.qData.tags.splice(index, 1);
        }
      },
      afterRead(file) {
        console.log(file,'file')
        // file.status = 'uploading';
        // file.message = '上传中...';
        //
        // setTimeout(() => {
        //   file.status = 'failed';
        //   file.message = '上传失败';
        // }, 1000);
      },
      formatDate(date) {
        return `${date.getFullYear()}.${date.getMonth() + 1}.${date.getDate()}`;
      },
      onConfirm(date) {
        const [start, end] = date;
        this.show = false;
        this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
        this.qData.activeStartTime = this.formatDate(start);
        this.qData.activeEndTime = this.formatDate(end);
      },
    }
  }
</script>

<style scoped>
  .tag-item+.tag-item{
    margin-left: 10px;
  }
.rich-text{
  height: 160px;
}
</style>



