import Vue from 'vue'
import Router from 'vue-router'

import GoodsList from '@/views/GoodsList'
import Cart from '@/views/Cart'
import Address from '@/views/Address'
import OrderConfirm from '@/views/OrderConfirm'
import OrderSuccess from '@/views/OrderSuccess'
import detail from '@/views/detail'
import login from '@/views/login'
import register from '@/views/register'
import myCenter from '@/views/myCenter'
import changePSW from '@/views/changePSW'
import list from '@/views/home'
import change from '@/views/change'
import sign from '@/views/sign'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'GoodsList',
      component:GoodsList
    },
    {
      path: '/cart',
      name: 'Cart',
      component:Cart
    }, {
      path: '/login',
      name: 'login',
      component:login
    },{
      path: '/register',
      name: 'register',
      component:register
    },
    {
      path: '/goods',
      name: 'GoodsList',
      component: GoodsList
    },{
      path: '/detail',
      name: 'GoodsDetail',
      component: detail
    },
    {
      path: '/address',
      name: 'Address',
      component:Address
    },
    {
      path: '/orderConfirm',
      name: 'OrderConfirm',
      component:OrderConfirm
    },
    {
      path: '/orderSuccess',
      name: 'OrderSuccess',
      component:OrderSuccess
    },
    {
      path: '/myCenter',
      name: 'myCenter',
      component:myCenter
    },
    {
      path: '/changePSW',
      name: 'changePSW',
      component:changePSW
    },
    {
      path: '/list',
      name: 'list',
      component:list
    },
    {
      path: '/change',
      name: 'change',
      component:change
    },
    {
      path: '/sign',
      name: 'sign',
      component:sign
    }
  ]
})



