<template>
  <div class="relative" style="height: 100%;overflow-y: auto">
    <div class="header width100 bg-w">
      <div class="btn-bm grey600 fontS" @click="back">返回</div>
    </div>
    <mu-container>
      <mu-card style="width: 100%; margin: 0 auto;padding: 51px 0;box-sizing: border-box">
        <mu-card-media>
          <img :src="detailData.productUrl">
        </mu-card-media>
        <mu-card-title :title="detailData.activeName" :sub-title="`${detailData.activeStartTime}-${detailData.activeEndTime} ${detailData.address}`">
        </mu-card-title>
        <div class="" style="padding: 0 16px">{{detailData.signed}}/{{detailData.limit}}人开团</div>
        <mu-card-text>
          {{detailData.text}}
        </mu-card-text>
        <mu-card-actions>
<!--          <mu-button flat>Action 2</mu-button>-->
        </mu-card-actions>
      </mu-card>
    </mu-container>
    <div class="footer width100">
      <div @click="isPopup = true" class="btn-bm2 width100 bg-red400 fontXL text-center">立即参加</div>
<!--      <div class="btn-bm2 width100 bg-red400 fontXL text-center" @click="isPopup = true">立即参加</div>-->
    </div>
    <div class="popup" v-if="isPopup">
      <div class="absolute-center bg-w" style="z-index: 9">
        <img src="/static/QRcord.jpg" style="width: 200px"/>
        <a href="http://wpa.qq.com/msgrd?v=3&uin=2691361970&site=qq&menu=yes" class="text-center fontL font-bold">或点击这里添加客服QQ:2691361970报名</a>
<!--        <div class="text-center fontXXL font-bold">报名码：46446</div>-->
      </div>
      <div class="bg" @click="isPopup = false"></div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "detail",
    data() {
      return {
        isPopup:false,
        detailData:null
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      this.getDetail(this.$route.query.id)
    },
    computed: {},
    methods: {
      back(){
        this.$router.back(-1)
      },
      getDetail(id){
        axios.get("/active/getActiveDetail",{params:{id:id}}).then((response)=>{
          var res = response.data;
          if(Number(res.status)===0){
            this.detailData = res.result
          }else{
          }
        });
      },
      close(){
        console.log(1)
        this.isPopup = false;
      }
    }
  }
</script>

<style scoped>
.btn-bm2{
  padding: 16px 32px;
  color: #eeeeee;
}
.btn-bm{
  display: inline-block;
  padding: 16px 32px;
  color: #eeeeee;
}
  .header{
    position: fixed;
    top: 0;
    z-index: 99;
  }
  .footer{
    position: fixed;
    z-index: 9;
    bottom: 0px;
  }
  .popup{
    position: fixed;
    right: 0;
    left: 0;
    bottom: 0;
    top: 0;
    z-index: 999;
  }
  .bg{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
  }
</style>



