<template>
  <div class="middleCenter middleCenter8" style="background: rgb(255, 255, 255);height: 100%;">
    <div class="formMiddleContent formMiddleContent8 moduleContent" _innertype="0">
      <div id="mLogin" style="position: relative">
        <div class="text-center padding-tb16 border-bottom-gray"><span
          class="fontL">修改信息</span></div>
        <div class="loginContent mloginContent flex">
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex" style="line-height: 50px;height: 50px;">
              cn：
              <input type="text" maxlength="50"
                     class="admin-text "
                     placeholder="请输入圈名"
                     v-model="params.cosName"
              >
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex" style="line-height: 50px;height: 50px;">
              手机号：
              <div class="">请联系客服修改</div>
            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex" style="line-height: 50px;height: 50px;">
              密码：
              <div class="">请联系客服修改</div>

            </div>
          </div>
          <div class="border-bottom-gray input-item">
            <div class="g_globalLine forAcctLogin input-item fontL flex" style="line-height: 50px;height: 50px;">
              <div style="white-space: nowrap">QQ或微信：</div>
              <input type="text" maxlength="50"
                     class="admin-text "
                     placeholder="请输入QQ或微信"
                     v-model="params.QQorWeChat"
              >
            </div>
          </div>
          <div class="g_globalLine forAcctLogin input-item text-center login-item" @click="updateUserInfo(params)">
            提交
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "register",
    data() {
      return {
        params:{

        }
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      if (this.$store.getters.getUserInfo){
        this.getUserInfo(this.$store.getters.getUserInfo.userName);
      }else {
        this.checkLogin(()=>{
          this.getUserInfo(this.$store.getters.getUserInfo.userName);
        });
      }
    },
    computed: {},
    methods: {
      checkLogin(cb){
        axios.get("/users/checkLogin").then((response)=>{
          var res = response.data;
          var path = this.$route.pathname;
          if(Number(res.status)===0){
            this.$store.commit("updateUserInfo",res.result);
            cb()
          }else{
            this.$router.push({path: '/login'})
          }
        });
      },
      getUserInfo(userName){
        axios.post("/users/getUserInfo",{userName:userName}).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.params = res.result;
          }
        })
      },
      updateUserInfo(data){
        axios.post("/users/updateUserInfo",data).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.params = res.result;
            this.$toast.success(res.msg);
            setTimeout(()=>{this.$router.back(-1)},1000)
          }
        })
      }
    }
  }
</script>

<style scoped>
  .loginContent{
    width: 100%;
    padding-top: 100px;
    align-items: center;
    flex-direction:column;
  }
  .login-item{
    margin: 30px 0px 17px 0;
    height: 50px;
    line-height: 50px;
    background: #292929;
    color: #eeeeee;
  }
  .input-item{
    width: calc(100% - 60px);
  }
</style>



