<template>
  <div class="h-100" style="overflow-y: auto">
    <van-cell-group>
      <van-field
              v-model="userName"
              center
              clearable
              label="查询用户"
              placeholder="请输入短信验证码"
              maxlength="11"
      >
        <template #button>
          <van-button size="small" type="primary" @click="getUserInfo(userName)">查询</van-button>
        </template>
      </van-field>
    </van-cell-group>
    <van-cell-group v-if="userInfo">
      <van-cell title="用户名" :value="userInfo.userName"/>
      <van-cell title="CN" :value="userInfo.cosName"/>
      <van-cell title="QQ或微信" :value="userInfo.QQorWeChat"/>
      <van-cell title="活动参加情况">
        <span class="active-text" v-for="(item,key) in userInfo.activeList" :key="key">{{new Date(item.activeStartTime) > new Date()?item.activeName:''}}</span>
        <span v-if="userInfo.activeList.length <= 0">无</span>
      </van-cell>
    </van-cell-group>
    <div class="" v-if="userInfo">
    <div class="" >可报名活动</div>
    <van-checkbox-group v-model="result">
      <van-cell-group>
        <van-cell
                v-for="(item, index) in goodsList"
                clickable
                :key="item"
                :title="`${item.activeName}`"
                @click="toggle(index)"
        >
          <template #right-icon>
            <van-checkbox :name="item" ref="checkboxes" />
          </template>
        </van-cell>
        <van-cell v-if="goodsList.length<=0">无</van-cell>
      </van-cell-group>
    </van-checkbox-group>
    </div>
    <div class="flex">
      <van-button type="primary" v-if="result.length>0" class="f-item" @click="updateUserInfo">提交</van-button>
      <van-button type="primary" v-else class="f-item" disabled>提交</van-button>
      <van-button type="warning" class="f-item" @click="$router.push('/list')">返回</van-button>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "sign",
    data() {
      return {
        userName:'',
        userInfo:null,
        goodsList:[],
        result: []
      }
    },
    created() {
    },
    destroyed() {
    },
    mounted() {
      this.getGoodsList()
    },
    computed: {},
    methods: {
      toggle(index) {
        this.$refs.checkboxes[index].toggle();
      },
      getUserInfo(userName){
        axios.post("/users/getUserInfo",{userName:userName}).then((response)=>{
          let res = response.data;
          if(res.status==="0"&&res.result){
            this.userInfo = res.result;
              for (let UIIdx in this.userInfo.activeList){
                for (let GLIdx in this.goodsList){
                  if (this.userInfo.activeList[UIIdx].activeName === this.goodsList[GLIdx].activeName){
                    this.goodsList.splice(GLIdx,1)
                  }
                }
              }
          }else {
            暂无用户
          }
        })
      },
      getGoodsList(flag){
        var param = {
          page:1,
          pageSize:9999,
          sort:this.sortFlag?1:-1,
          priceLevel:''
        };
        // this.loading = true;
        this.$axios.get("/active/list",{
          params:param
        }).then((response)=>{
          var res = response.data;
          // this.loading = false;
          if(res.status==="0"){
            if(flag){
              this.goodsList = this.goodsList.concat(res.result.list);

              if(res.result.count===0){
                this.busy = true;
              }else{
                this.busy = false;
              }
            }else{
              this.goodsList = res.result.list.filter(i=>new Date(i.activeStartTime)>new Date());
              this.busy = false;
            }
          }else{
            this.goodsList = [];
          }
        });
      },
      updateUserInfo(e,data=JSON.parse(JSON.stringify(this.userInfo))){
        data.activeList = data.activeList.concat(this.result)
        axios.post("/users/updateUserActive",data).then((response)=>{
          let res = response.data;
          if(res.status==="0"){
            this.params = res.result;
            this.$toast.success(res.msg);
            // setTimeout(()=>{this.$router.push('/list')},1000)
            this.getUserInfo(this.userName)
          }
        })
      }
    }
  }
</script>

<style scoped>
  .active-text+.active-text{
    padding-left: 10px;
  }
</style>



